        #t_2_1.h4 2.1 MAGERIT
      .col-12.col-lg-7.align-self-center
        p.mt-3 MAGERIT es una metodologa que se esfuerza por enfatizar en dividir los activos de la organizacin en variados grupos, para identificar ms riesgos y poder tomar contramedidas para evitar as cualquier inconveniente.
        p.mt-3 La razn de ser de MAGERIT est directamente relacionada con la generalizacin del uso de las tecnologas de la informacin, que supone unos beneficios evidentes para los ciudadanos; pero tambin da lugar a ciertos riesgos que deben minimizarse con medidas de seguridad que generen confianza. 
        p.mt-3 En el periodo transcurrido desde la publicacin de la primera versin de MAGERIT (1997) hasta la fecha, el anlisis de riesgos se ha venido consolidando como paso necesario para la gestin de la seguridad.
        p.mt-3 La Evaluacin del riesgo es fundamental para llevar a cabo planes de seguridad y de contingencia dentro de la organizacin, para poder gestionarlos y hacerse riguroso frente a posibles ataques a los datos y la informacin tanto de la organizacin, como de los servicios que presta.
