        #t_2_2.h4 2.2	Metodologa OCTAVE (Operationally Critical Threat, Asset and Vulnerability Evaluation)
      .col-12.col-lg-7.align-self-center.px-5
        p.mt-3  Es un mtodo de evaluacin y de gestin de los riesgos para garantizar la seguridad del sistema informtico, desarrollado por el estndar internacional ISO270001.
        p.mt-3 El ncleo central de OCTAVE es un conjunto de criterios (principios, atributos y resultados) a partir de los cuales se pueden desarrollar diversas metodologas.
      .col-2.d-none.col-lg-4.d-lg-block.mt-4.offset-1
