        img(src='@/assets/template/tema-2-13.png', alt='').py-4.px-5
    .row
      .titulo-segundo.mt-5
        #t_2_3.h4 2.3 Metodologa CORAS (Construct a platform for risk analysis of security critical system)
      p.mt-3 Desarrollado a partir de 2001 por SINTEF, un grupo de investigacin noruego financiado por organizaciones del sector pblico y privado. Se desarroll en el marco del Proyecto CORAS (IST-2000-25031) financiado por la Unin Europea.
    .row.mt-3  
      .col-3.d-none.d-md-flex.offset-1
        img(src='@/assets/template/tema-2-14.svg', alt='')
      .col-12.col-md-8.mt-4
        p.px-4 #[strong El mtodo CORAS proporciona:]
        ul.lista-ul.px-4.mt-3
          li.mb-0
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p Una metodologa de anlisis de riesgos basado en la elaboracin de modelos, que consta de siete pasos, basados fundamentalmente en entrevistas con los expertos.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p Un lenguaje grfico basado en UML (Unified Modelling Language) para la definicin de los modelos (activos, amenazas, riesgos y salvaguardas), y guas para su utilizacin a lo largo del proceso. El lenguaje se ha definido como un perfil UML.
          li.mb-0.mt-2 
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p Un editor grfico para soportar la elaboracin de los modelos, basado en Microsoft Visio.
          li.mb-0.mt-2
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p Una biblioteca de casos reutilizables.
          li.mb-0.mt-2
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p  Una herramienta de gestin de casos, que permite su gestin y reutilizacin.
          li.mb-0.mt-2
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p Representacin textual basada en XML (eXtensible Mark-up Language) del lenguaje grfico.
          li.mb-0.mt-2
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
