        p #[strong El mtodo propone 7 pasos que son:]
        .row.mt-4.border-top-gris.py-3.mx-md-3
          .col-2.align-self-center
            h4.color-c.text-center Paso 1
          .col-10
            p #[strong Presentacin:] reunin inicial, para presentar los objetivos y el alcance del anlisis y recabar informacin inicial.
        .row.border-top-gris.py-3.mx-md-3
          .col-2.align-self-center
            h4.color-c.text-center Paso 2
          .col-10
            p #[strong Anlisis de alto nivel: ] entrevistas para verificar la comprensin de la informacin obtenida y la documentacin analizada. Se identifican amenazas, vulnerabilidades, escenarios e incidentes. 
        .row.border-top-gris.py-3.mx-md-3
          .col-2.align-self-center
            h4.color-c.text-center Paso 3
          .col-10
            p #[strong Aprobacin:] descripcin detallada de los objetivos, alcance y consideraciones, para su aprobacin por parte del destinatario del anlisis de riesgos.
        .row.border-top-gris.py-3.mx-md-3
          .col-2.align-self-center
            h4.color-c.text-center Paso 4
          .col-10
            p #[strong Identificacin de riesgos:] identificacin detallada de amenazas, vulnerabilidades, escenarios e incidentes.
        .row.border-top-gris.py-3.mx-md-3
          .col-2.align-self-center
            h4.color-c.text-center Paso 5
          .col-10
            p #[strong Estimacin de riesgo:] estimacin de probabilidades e impactos de los incidentes identificados en el paso anterior.
        .row.border-top-gris.py-3.mx-md-3
          .col-2.align-self-center
            h4.color-c.text-center Paso 6
          .col-10
            p #[strong Evaluacin de riesgo:] emisin del informe de riesgos, para su ajuste fino y correcciones.
        .row.border-top-gris.py-3.mx-md-3
          .col-2.align-self-center
            h4.color-c.text-center Paso 7
          .col-10
            p #[strong Tratamiento del riesgo: ] identificacin de las salvaguardas necesarias, y realizacin de anlisis coste/beneficio.
        .row.mt-4
