            img(src='@/assets/template/tema-2-15.png', alt='')
    .row
      .titulo-segundo.mt-5
        #t_2_4.h4 2.4 Metodologa MEHARI
      p.mt-4 MEHARI es una metodologa desarrollada por CUSIF (Club de la Securit De Linformation Franais) en 1998 que pas a ser open source en 2007.
      p.mt-4 El objetivo de esta metodologa es permitir un anlisis directo e individual de situaciones de riesgos descritas en diferentes escenarios y proporcionar un completo conjunto de herramientas especficamente diseadas para la gestin de la seguridad a corto, medio y largo plazo, adaptables a diferentes niveles de madurez.
      .h5.mt-5 2.4.1 Introduccin general de la utilizacin de MEHARI
      p.mt-4 La principal orientacin de MEHARI es la evaluacin y reduccin de riesgos. Sus bases de datos de conocimiento, mecanismos y herramientas se han creado con ese objetivo.
      p.mt-4 Asimismo, en la mente de los diseadores del conjunto de la metodologa, la necesidad de un mtodo estructurado para el anlisis y reduccin de riesgos puede ser, en funcin de la organizacin:
      .col-8.offset-2.mt-5
        img(src='@/assets/template/tema-2-16.png', alt='')
      p.mt-5 Con esto en mente, MEHARI proporciona un conjunto de enfoques y herramientas que permiten realizar un anlisis de riesgos cuando es necesario.
      p.mt-4 La metodologa MEHARI, que comprende las bases de datos de conocimiento, los manuales y las guas que describen los diferentes mdulos (amenazas, riesgos, vulnerabilidades), se encuentra disponible para ayudar a las personas implicadas en la gestin de la seguridad (CISO, responsables de riesgos, auditores y CIO), en sus diferentes tareas y actividades.

      .col-10.offset-1.rounded(style="background: linear-gradient(70deg, #9B235C, #531331);").px-5.py-4.mt-4
        p.text-white MEHARI es un conjunto de herramientas y funcionalidades metodolgicas para la gestin de la seguridad y de las medidas asociadas, basado en un anlisis de riesgos preciso. Los aspectos fundamentales de MEHARI son:
        ul.lista-ul.px-4.mt-3
          li.mb-0 
            i.fas.fa-angle-right.color-white
            | #[strong.text-white.font-normal Su modelo de riesgos (cualitativo y cuantitativo),]
          li.mb-0 
            i.fas.fa-angle-right.color-white
            | #[strong.text-white.font-normal El examen de la eficacia de las medidas de seguridad en vigor o previstas,]
          li.mb-0 
            i.fas.fa-angle-right.color-white
            | #[strong.text-white.font-normal La capacidad para evaluar y simular los niveles de riesgo derivados de medidas adicionales]
    .row
      .h5.mt-5 2.4.2 Fases de la metodologa MEHARI      
    .row
      .col-11.border-y-morado.bg-amarillo-claro.p-4.mt-4.mx-5
        .row
          .col-12.col-md-8 
            .h6 2.4.2.1 Anlisis o evaluacin de Riesgos
            p.mt-3 Una situacin de riesgo se puede caracterizar por diferentes factores:
            ul.lista-ul.px-4.mt-3
              li.mb-0 
                .row
                  .col-1.text-align-right
                    i.fas.fa-angle-right.color-c.text-center
                  .col-11
                    p Factores estructurales (u organizacionales), los cuales no dependen de las medidas de seguridad, sino de la actividad principal de la organizacin, su entorno y su contexto.
              li.mb-0
                .row
                  .col-1.text-align-right
                    i.fas.fa-angle-right.color-c.text-center
                  .col-11
                    p Factores de la reduccin de riesgo, que son funcin directa de las medidas de seguridad implementadas.
            p.mt-4 Para ello, integra herramientas (como criterios de evaluacin, frmulas, etc.) y bases de datos de conocimiento (en particular para el diagnstico de las medidas de seguridad), como complemento esencial al marco de anlisis de riesgos.
            p.mt-4 Es necesario realizar un enfoque estructurado que permita identificar todas las situaciones potenciales de riesgo, con el fin de analizar las ms crticas y poder identificar las acciones para reducir el riesgo a niveles aceptables.
          .col-4.d-none.d-md-flex
            img(src='@/assets/template/tema-2-17.svg', alt='').w-75
      .col-11.border-y-morado.bg-amarillo-claro.p-4.mt-3.mx-5
        .row
          .h6 2.4.2.2 Evaluaciones de Seguridad
          .col-4.d-none.d-md-flex.px-5.mt-4
            img(src='@/assets/template/tema-2-18.svg', alt='').w-75
          .col-12.col-md-8.align-self-center
            p MEHARI integra cuestionarios de controles de seguridad, lo que permite evaluar el nivel de calidad de los mecanismos y soluciones encaminadas a la reduccin del riesgo. Los controles o medidas de seguridad se agrupan en servicios y en dominios de seguridad. Para realizar esta evaluacin es necesario seguir los siguientes pasos:
          ul.lista-ul.px-5.mt-5
            li.mb-0 
              .row
                .col-1.text-align-right
                  i.fas.fa-angle-right.color-c.text-center
                .col-11
                  p Revisin de vulnerabilidades o evaluacin de los servicios de seguridad: MEHARI proporciona un modelo de riesgos estructurado que considera los factores de reduccin del riesgo en forma de servicios de seguridad. #[br] #[br] El resultado de la evaluacin de la vulnerabilidad tendr el fin de garantizar que los servicios de seguridad cumplen realmente su cometido. #[br] #[br] La evaluacin se basa en una base de datos experta de conocimientos proporcionada por MEHARI para evaluar el nivel de calidad de las medidas de seguridad.
            li.mb-0.mt-4
              .row
                .col-1.text-align-right
                  i.fas.fa-angle-right.color-c.text-center
                .col-11
                  p Planes de seguridad basados en la revisin de vulnerabilidades: se realizar la confeccin de planes de seguridad como resultado directo de la evaluacin del estado de los servicios de seguridad. #[br] #[br] El proceso de gestin de la seguridad se enfoca en ejecutar una evaluacin y decidir mejorar todos aquellos servicios que no tienen un suficiente nivel de calidad. #[br] #[br] MEHARI proporciona unos cuestionarios de diagnstico que se pueden utilizar para este tipo de enfoque.
            li.mb-0.mt-4
              .row
                .col-1.text-align-right
                  i.fas.fa-angle-right.color-c.text-center
                .col-11
                  p Apoyo en las BBDD en la creacin de un marco de referencia de seguridad: las bases de datos de conocimiento de MEHARI se pueden utilizar directamente para crear un marco de referencia de seguridad que contendr y describir el conjunto de reglas e instrucciones de seguridad que debe seguir la organizacin. #[br] #[br] Los cuestionarios de evaluacin de MEHARI son una buena base de trabajo para los responsables de seguridad para decidir lo que debe ser aplicado en la organizacin. #[br] #[br] La creacin de un conjunto de reglas, a travs de un marco de referencia de seguridad, se enfrenta a menudo a dificultades en la implementacin local, por lo que se deben gestionar exenciones y excepciones.
            li.mb-0.mt-4
              .row
                .col-1.text-align-right
                  i.fas.fa-angle-right.color-c.text-center
                .col-11
                  p Dominios cubiertos por el mdulo de evaluacin de vulnerabilidades: desde un punto de vista de anlisis de riesgo, en base a la identificacin de todas las situaciones de riesgo y con el deseo de cubrir todos aquellos riesgos inaceptables, MEHARI no se limita simplemente al dominio IT. #[br] #[br] El mdulo de evaluacin cubre, adems de los sistemas de informacin, todo el conjunto de la organizacin, como la proteccin del sitio en general, el entorno de trabajo y los aspectos legales y regulatorios.
      .col-11.border-y-morado.bg-amarillo-claro.p-4.mt-3.mx-5
        .row
          .col-12.col-md-8 
            .h6  2.4.2.3 Anlisis de amenazas
            p.mt-4 Sea cual sea la orientacin de la poltica de seguridad, hay un principio en el que coinciden todos los responsables: debe existir un equilibrio entre las inversiones de seguridad por un lado y la importancia de los principales retos empresariales por el otro.
            p.mt-4 Esto significa que la comprensin de las amenazas del negocio es fundamental, y que el anlisis del contexto de seguridad merece un nivel prioritario y un mtodo estricto y riguroso de evaluacin.
            p.mt-4 El fin del anlisis de las amenazas de seguridad es responder a la siguiente doble pregunta Qu puede suceder, y si sucede, puede ser serio?
            p.mt-4 MEHARI proporciona un mdulo de anlisis de amenazas con dos tipos de resultados:
          .col-4.d-none.d-md-flex.align-self-center
            img(src='@/assets/template/tema-2-19.svg', alt='').w-75 
        ul.lista-ul.mt-5
          li.mb-0 
            .row
