                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p Una escala de valores de posibles malfuncionamientos en sus procesos operacionales.
          li.mb-0.mt-2
            .row
              .col-1.text-align-right
                i.fas.fa-angle-right.color-c.text-center
              .col-11
                p Una clasificacin de la informacin y de los activos TI: consiste en la definicin, para cada tipo de informacin, para cada tipo de activo TI, y para cada criterio de clasificacin (habitualmente Confidencialidad, Integridad y Disponibilidad). La clasificacin de la informacin de los activos es la escala de los valores de malfuncionamiento definida anteriormente traducido a indicadores de sensibilidad asociados con los activos TI.
        p.mt-4 La escala de valores de malfuncionamiento y la clasificacin de la informacin y activos son dos formas distintas de expresar las amenazas de seguridad. El primero es ms detallado y proporciona ms informacin a los CISO y el segundo es ms generalista y resulta ms til para las campaas de sensibilizacin e informacin.
