    .h5.mt-5 2.5.2 Identificacin de vulnerabilidades
    p.mt-4 Las vulnerabilidades son debilidad de cualquier tipo que compromete la seguridad del sistema informtico, para la identificacin de vulnerabilidades se puede recurrir a dos estrategias distintas pero complementarias que son a partir de la utilizacin de herramientas tecnolgicas que de manera automtica pueden identificarlas, este proceso se realiza mediante el cumplimiento de tcnicas de ethical hacking siguiendo una metodologa para este proceso como ISSAF, OWASP, OSSTMM, entre otras.
    p.mt-4 Sin embargo, existen vulnerabilidades que deben identificarlas y que ninguna herramienta tecnolgica las puede detectar como las que se generan por problemas elctricos, de control de acceso fsico a servidores y equipos, falta de documentacin de los procesos y procedimientos, entre otros que solo pueden identificarse mediante un anlisis personal, observacin directa, entrevista dirigida y otras estrategias de manera presencial.
