    .h5.mt-5 2.5.3 Determinacin de probabilidades
    p.mt-4 Las probabilidades permiten medir en qu porcentajes se pueden llegar a presentar algn tipo de incidente que ponga en riesgo el correcto funcionamiento de los recursos tecnolgicos.
    p.mt-4 Al igual que con las variables anteriores para su medicin nos apoyaremos en las siguientes caractersticas:
