    .h5.mt-5 2.5.4 Anlisis de impacto
    p.mt-4  Para determinar el establecimiento de prioridades en la proteccin y prevencin, se requiere conocer cual o cuales activos pueden ser potencialmente afectados por incidentes de seguridad. 
    p.mt-4 En caso de que haya afectaciones a los activos se debe determinar cul es la afectacin y grado de problema que podra causar en la organizacin, este grado de afectacin puede medirse desde las consecuencias econmicas, indisponibilidades de acceso entre otras.
    p.mt-4 El impacto debe medirse con base a los tres principios de la seguridad: la confidencialidad, la integridad y la disponibilidad.
    p.mt-4 El impacto se puede medir en tres niveles que son:
    h6.text-center.mt-5 Criterios para determinar el Impacto
