                p Provoca la desarticulacin de un componente de la organizacin. Si no se atiende a tiempo, a largo plazo puede provocar la desarticulacin de la organizacin.
            tr
              th
                p.text-center BAJO
              th 
                p Causa dao aislado, que no perjudica a ningn componente de la organizacin.
