    .h5.mt-5 2.5.5 Determinacin del riesgo
    p.mt-4  El riesgo es el efecto negativo cuando se produce un impacto sobre un activo, teniendo en cuenta tanto la probabilidad de la amenaza y el impacto de esta. Una efectiva administracin de riesgos se necesita para evaluar y mitigar los riesgos identificados en la empresa.

